import * as React from 'react';

import Layout from '../components/layout';
import Seo from '../components/seo';
import {
  Box,
  Flex,
  Heading,
  Grid,
  GridItem,
  Text,
  Link,
} from '@chakra-ui/react';
import RlCard from '../components/rlCard/rlCard';
import { ArrowForwardIcon } from '@chakra-ui/icons';
import { navigate } from 'gatsby';
import config from '../../config';

const IndexPage = ({ location }) => {
  const repos = config.LANDING_PAGE_CARDS;

  return (
    <Layout location={location}>
      <Box py={20}>
        <Box h="100%" w="100%" maxW="70rem" mr="auto" ml="auto">
          <Heading as="h1">
            Welcome to the RadiantOne Identity Data Platform!
          </Heading>
          <Text fontSize="xl">
            Harness your identity data with intelligent integration to drive
            better business outcomes, improve security and compliance posture,
            increase speed-to-market, and more. Click below to learn about the
            detailed offerings and access the documentation to help get you
            started.
          </Text>
          <Box background="black" mt="10">
            <Box
              h="100%"
              maxW="100%"
              backgroundImage="./frontPageBg.svg"
              bgPosition="bottom"
              bgRepeat="no-repeat"
              pb="3em"
            >
              <Box textAlign="center">
                <Text
                  color="white"
                  fontSize="3xl"
                  fontWeight="800"
                  paddingTop="2em"
                >
                  One Platform
                </Text>
                <Text color="white" fontWeight="800" fontSize="2xl">
                  It&apos;s a radically simple approach to identity data
                  management and analytics.
                </Text>
              </Box>

              <Grid
                gap="2em"
                templateColumns={{
                  base: '1fr',
                  md: 'repeat(2, 1fr)',
                  sm: '1fr',
                }}
                style={{ marginTop: '2em', marginBottom: '2em' }}
                px="3em"
                maxW="920px"
                marginX="auto"
              >
                {repos.map((repo, index) => (
                  <GridItem
                    h="100%"
                    key={repo.name + index}
                    gridColumn={{
                      base: 'auto',
                      md:
                        index === repos.length - 1 && repos.length % 2 !== 0
                          ? 'span 2'
                          : 'auto',
                    }}
                  >
                    <RlCard title={repo.displayName} text={repo.description}>
                      <Flex flex={1} alignItems="end">
                        <Box>
                          {repo.links?.map(link => (
                            <Flex
                              key={link.text}
                              direction="row"
                              alignItems="center"
                              pt="1em"
                              _groupHover={{ color: 'white' }}
                            >
                              <ArrowForwardIcon
                                boxSize="1.5em"
                                color="rlGreen"
                                _groupHover={{ color: 'white' }}
                              />
                              <Link
                                variant="link"
                                as="div"
                                fontWeight={700}
                                fontSize="lg"
                                color="rlGreen"
                                _groupHover={{ color: 'white' }}
                                onClick={() => {
                                  navigate(link.href);
                                }}
                              >
                                {link.text}
                              </Link>
                            </Flex>
                          ))}
                        </Box>
                      </Flex>
                    </RlCard>
                  </GridItem>
                ))}
              </Grid>
            </Box>
          </Box>
        </Box>
      </Box>
    </Layout>
  );
};

export const Head = () => <Seo title="Home" />;

export default IndexPage;
